html, body {
  -webkit-app-region: no-drag;
  color: var(--outerspace);
  background: radial-gradient(circle at 100% 0, #427bb7 0%, #01071a 50%) 100% 0;
  border: 0;
  margin: 0;
  font-family: MainFont;
  font-weight: 300;
  overflow: hidden;
}

div {
  user-select: none;
  cursor: default;
}

::-webkit-scrollbar {
  background: none;
  width: 0;
}

.dash {
  background: var(--ghostA);
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden scroll;
}

.dash .dashMain {
  z-index: 1000;
  background: var(--ghostZ);
  transition: var(--standard);
  border-radius: 32px;
  padding-bottom: 20px;
  position: absolute;
  inset: 80px 8px 40px;
}

.dash .dashMain .dashMainScroll {
  z-index: 2000;
  border-radius: 32px;
  position: absolute;
  inset: 0;
  overflow: hidden scroll;
}

.dash .dashMain .dashMainOverlay {
  z-index: 40000000;
  box-shadow: inset 0px 0px 6px var(--ghostY);
  pointer-events: none;
  border-radius: 32px;
  position: absolute;
  inset: 0;
}

.dash .dashMain .originTitle {
  align-items: center;
  margin: 30px 8px 5px 0;
  font-size: 18px;
  font-weight: 400;
  display: flex;
}

.dash .dashMain .originTitle .originTitleIcon {
  margin: 9px 16px;
}

.dash .dashMain .originTitle .originTitleText {
  margin-top: -2px;
}

.dash .dashMain .clearOriginsButton {
  background: var(--ghostA);
  box-sizing: border-box;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  border-bottom: 2px solid var(--ghostZ);
  height: 38px;
  box-shadow: 0px 2px 4px var(--ghostY);
  letter-spacing: 1px;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  margin: 74px 6px 5px;
  padding: 18px 16px 16px 17px;
  font-size: 13px;
  font-weight: 500;
  display: flex;
  transform: translateY(0);
}

.dash .dashMain .clearOriginsButton * {
  pointer-events: none;
}

.dash .dashMain .clearOriginsButton:hover {
  background: var(--ghostB);
  box-shadow: 0px 4px 8px var(--ghostY);
  transform: translateY(-1px);
}

.dash .dashMain .clearOriginsButton:active {
  background: var(--ghostB);
  box-shadow: 0px 1px 2px var(--ghostY);
  transform: translateY(0);
}

.dash .dashFooter {
  background: var(--ghostZ);
  box-sizing: border-box;
  border-radius: 30px;
  height: 60px;
  padding: 3px 3px 1px;
  animation: .64s ease-in-out showFooter;
  position: absolute;
  bottom: 40px;
  left: 8px;
  right: 8px;
}

.dash .dashFooter .dashFooterButton {
  background: var(--ghostA);
  cursor: pointer;
  height: 56px;
  box-shadow: 0px 2px 4px var(--ghostY);
  border-bottom: 2px solid var(--ghostZ);
  box-sizing: border-box;
  letter-spacing: 1px;
  color: var(--outerspace);
  z-index: 5;
  border-radius: 28px;
  justify-content: center;
  align-items: center;
  font-family: MainFont;
  font-size: 16px;
  font-weight: 400;
  display: flex;
  position: relative;
  transform: translateY(0);
}

.dash .dashFooter .dashFooterButton * {
  pointer-events: none;
}

.dash .dashFooter .dashFooterButton .newAccountIcon {
  justify-content: center;
  align-items: center;
  width: 64px;
  font-size: 28px;
  font-weight: 300;
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
}

.dash .dashFooter .newAccount:hover {
  background: var(--ghostB);
}

.dash .dashFooterButton:hover {
  box-shadow: 0px 4px 8px var(--ghostY);
  background: var(--ghostB);
  transform: translateY(-1px);
}

.dash .dashFooterButton:active {
  box-shadow: 0px 1px 2px var(--ghostY);
  background: var(--ghostB);
  transform: translateY(0);
}

.dash .panelScrollOverlay {
  z-index: 40000000;
  box-shadow: inset 0px 0px 6px var(--ghostX);
  pointer-events: none;
  border-radius: 16px;
  position: absolute;
  inset: 0;
}

.dash .addAccounts {
  background: var(--ghostZ);
  z-index: 4000;
  padding-bottom: 60px;
  position: absolute;
  inset: 0;
  overflow: hidden scroll;
}

.dash .addAccounts .addAccountsHeader .addAccountsHeaderTitle {
  justify-content: center;
  align-items: center;
  height: 50px;
  margin-top: 20px;
  margin-bottom: 20px;
  font-size: 14px;
  font-weight: 400;
  display: flex;
}

.dash .addAccounts .addAccountsHeader .addAccountsHeaderClose {
  cursor: pointer;
  background: var(--ghostB);
  text-transform: uppercase;
  letter-spacing: 1px;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 90px;
  height: 30px;
  padding-bottom: 1px;
  padding-left: 1px;
  font-size: 11px;
  font-weight: 300;
  display: none;
  position: absolute;
  top: 18px;
  right: 10px;
}

.dash .addAccounts .addAccountsHeader .addAccountsHeaderClose * {
  pointer-events: none;
}

.dash .addAccounts .addAccountsHeader .addAccountsHeaderClose:hover {
  background: var(--ghostC);
}

.dash .addAccounts .accountTypeSelect {
  cursor: pointer;
  background: var(--ghostA);
  height: 100px;
  box-shadow: 0px 4px 8px var(--ghostY);
  border-bottom: 2px solid var(--ghostZ);
  border-radius: 27px;
  justify-content: center;
  align-items: center;
  margin: 6px 6px 8px;
  font-size: 16px;
  font-weight: 400;
  display: flex;
  position: relative;
  transform: translateY(0);
}

.dash .addAccounts .accountTypeSelect * {
  pointer-event: none;
}

.dash .addAccounts .accountTypeSelect .accountTypeSelectIcon {
  margin-right: 16px;
}

.dash .addAccounts .accountTypeSelect:hover {
  box-shadow: 0px 4px 12px var(--ghostY);
  background: var(--ghostB);
  z-index: 200;
  transform: translateY(-1px);
}

.dash .addAccounts .accountTypeSelect:active {
  box-shadow: 0px 2px 4px var(--ghostY);
  z-index: 201;
  transform: translateY(0);
}

.dash .signers {
  width: 100%;
}

.dash .signers .signersHeader {
  box-sizing: border-box;
  text-transform: uppercase;
  letter-spacing: 1px;
  justify-content: center;
  align-items: center;
  width: calc(100% - 32px);
  height: 32px;
  margin: 0 16px 12px;
  font-size: 12px;
  font-weight: 400;
  display: flex;
  position: relative;
}

.dash .signers .noSigners {
  background: var(--ghostA);
  box-sizing: border-box;
  letter-spacing: 2px;
  text-transform: uppercase;
  width: 100%;
  height: 48px;
  box-shadow: 0px 4px 8px var(--ghostY);
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  margin: 0 0 40px;
  font-size: 11px;
  font-weight: 400;
  display: flex;
  position: relative;
  overflow: hidden;
}

.dash .signers .signersMid {
  padding: 0 6px 64px;
}

.dash .signers .signersList {
  flex-wrap: wrap;
  display: flex;
}

.network {
  box-shadow: 0px 4px 8px var(--ghostY);
  background: var(--ghostAZ);
  box-shadow: 0px 4px 6px var(--ghostY);
  border-bottom: 2px solid var(--ghostZ);
  border-radius: 27px;
  margin: 6px 6px 4px;
  position: relative;
}

.network .chainModules {
  padding-bottom: 0;
}

.phaseNetworkLine {
  height: 84px;
  position: relative;
}

.localSettings {
  transition: var(--standardFast);
  background: radial-gradient(circle at 100% 0, #427bb7 0%, #01071a 50%) 100% 0;
  flex-direction: column;
  justify-content: center;
  display: flex;
  position: absolute;
  inset: 0;
  transform: translateZ(0);
}

.localSettings .logoImage {
  width: 100%;
  height: auto;
  margin-bottom: 16px;
  display: block;
}

.localSettings .customInput {
  text-align: center;
  width: 100%;
  height: 100%;
