  background: none;
  border: none;
  outline: none;
  font-family: MainFont;
  font-size: 16px;
  font-weight: 300;
  position: absolute;
  inset: 0;
}

.localSettings .customInput div {
  z-index: 3;
  justify-content: center;
  align-items: center;
  display: flex;
}

.localSettings .phaseNetworkRemove:hover {
  background: var(--bad);
}

.localSettings .phaseNetworkSubmit {
  width: 60px;
  position: absolute;
  top: 45px;
  bottom: 1px;
  right: 0;
}

.localSettings .phaseNetworkSubmit svg {
  z-index: 2;
  position: relative;
}

.localSettings .phaseNetworkName {
  position: absolute;
  inset: 1px 160px 40px 0;
}

.localSettings .phaseNetworkName input {
  text-align: left;
  font-size: 22px;
  font-weight: 300;
}

.localSettings .phaseNetworkSymbol {
  width: 60px;
  position: absolute;
  top: 0;
  bottom: 40px;
  right: 0;
}

.localSettings .phaseNetworkSymbol input {
  font-family: FiraCode;
  font-size: 16px;
}

.localSettings .phaseNetworkId {
  width: 60px;
  position: absolute;
  top: 0;
  bottom: 40px;
  right: 80px;
}

.localSettings .phaseNetworkId input {
  font-family: FiraCode;
  font-size: 16px;
}

.localSettings .phaseNetworkExplorer {
  position: absolute;
  inset: 45px 80px 0 0;
}

.localSettings .phaseNetworkExplorer input {
  text-align: left;
}

.localSettings .localSettingsAddNetwork {
  z-index: 9999999;
  text-transform: uppercase;
  background: var(--ghostD);
  width: 26px;
  height: 26px;
  box-shadow: cardDrop, 0px 2px 1px var(--thin);
  transition: var(--standard), 0s linear background;
  cursor: pointer;
  border-radius: 13px;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-size: 23px;
  font-weight: 300;
  display: flex;
  position: absolute;
  right: 145px;
  overflow: hidden;
  transform: translateZ(0);
}

.localSettings .localSettingsAddNetwork:hover {
  background: var(--ghostD);
}

.localSettings .localSettingsWrapFadeTop {
  pointer-events: none;
  z-index: 5;
  height: 140px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.localSettings .localSettingsWrapFadeBot {
  pointer-events: none;
  z-index: 5;
  height: 140px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.localSettings .localSettingsWrap {
  z-index: 2;
  position: relative;
  overflow: hidden scroll;
}

.localSettings .localSettingsTitle:before {
  content: "";
  background: var(--thin);
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: -2px;
  left: 10px;
  right: 10px;
}

.localSettings .localSettingsTitle {
  align-items: flex-start;
  height: 30px;
  margin: 67px 0 0;
  padding: 0 17px 17px;
  font-size: 26px;
  font-weight: 300;
  display: flex;
  position: relative;
}

.localSettings .localSettingsTitle .localSettingsTitleText {
  margin-top: -5px;
}

.signerSetting {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 0 20px;
  display: flex;
}

.settingsTitle {
  background: #fffc;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 20px 0;
  display: flex;
}

.settingsItems {
  background: #0000321a;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 20px 0;
  display: flex;
}

.settingsItem {
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  padding: 20px;
  display: flex;
}

.discordInvite:hover .discordLink {
  cursor: pointer;
  position: relative;
  transform: translateY(-2px);
}

.discordInvite {
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  width: calc(100% - 16px);
  margin: 30px 8px;
  font-size: 13px;
  font-weight: 300;
  display: flex;
}

.discordInvite .discordLink {
  transition: var(--standardFast);
  padding-left: 5px;
  font-weight: 300;
  transform: translateY(0);
}

.quitFrame {
  margin-top: 30px;
  margin-bottom: 30px;
  padding: 0;
}

.connectionLevels {
  z-index: 999999999;
  width: 100%;
  min-width: 100%;
  position: relative;
}

.connectionLevels .connectionLevel {
  position: relative;
}

.connectionLevels .connectionTitle {
  text-transform: uppercase;
  letter-spacing: 1px;
  height: 12px;
  color: var(--outerspace08);
  justify-content: center;
  align-items: center;
  margin-left: -1px;
  padding: 8px 16px;
  font-size: 8px;
  font-weight: 300;
  display: flex;
}

.infoContainer {
  border-radius: 12px;
  margin: 10px 10px 12px;
  font-size: 14px;
  font-weight: 300;
  position: relative;
}

.chainCurrencyItem {
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.chainCurrencyItemSymbol, .sliceChainIdNumber {
  font-size: 13px;
  font-weight: 400;
}

.chainCurrencyItemAt {
  color: var(--good);
  margin: -2px 8px 0;
  font-size: 12px;
  font-weight: 400;
}

.sliceContainer {
  background: var(--ghostZ);
  border-radius: 20px;
  margin: 0 6px 12px;
  padding: 3px 0;
  font-size: 14px;
  font-weight: 300;
  position: relative;
}

.sliceContainer .sliceContainerClose {
  cursor: pointer;
  color: #000;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  height: 16px;
  display: flex;
}

.sliceContainer .sliceContainerClose div {
  pointer-events: none;
  margin: 0 18px;
  position: relative;
  top: 2px;
}

.sliceContainer .sliceContainerClose:hover {
  color: var(--white);
}

.sliceContainer .sliceTileCenter {
  justify-content: center;
}

.sliceContainer .sliceTile {
  cursor: pointer;
  transition: var(--standardFast);
  background: var(--ghostA);
  box-sizing: border-box;
  border-radius: 16px;
  justify-content: space-between;
  align-items: center;
  height: 64px;
  font-size: 12px;
  font-weight: 300;
  display: flex;
  position: relative;
  transform: scale(1);
}

.sliceContainer .sliceTile * {
  pointer-events: none;
}

.sliceContainer .sliceTile:first-child {
  margin: 0 3px;
}

.sliceContainer .sliceTileHighlight {
  background: var(--ghostB);
}

.sliceContainer .sliceTileClickable:hover {
  background: var(--ghostB);
  box-shadow: 0px 2px 2px var(--ghostZ);
}

.sliceContainer .sliceTileClickable:active {
  transform: translateY(0);
}

.sliceTileConnectionName {
  color: var(--outerspace);
  text-transform: uppercase;
  letter-spacing: 1px;
  padding: 1px 20px 0;
  font-size: 14px;
  font-weight: 400;
}

.sliceTileBlock {
  align-items: center;
  padding-right: 11px;
  font-family: FiraCode;
  font-size: 14px;
  font-weight: 300;
  display: flex;
  position: relative;
  top: 1px;
  bottom: 0;
}

.sliceTileBlock .sliceTileBlockIcon {
  color: var(--good);
  padding-right: 5px;
}

.sliceTileBlock .sliceTileChainId {
  padding-right: 15px;
}

.sliceTileIndicator {
  color: var(--outerspace);
  cursor: pointer;
  background: var(--good);
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  width: 5px;
  height: 5px;
  margin-top: -2px;
  display: flex;
  position: absolute;
  top: 50%;
  bottom: 0;
  left: 13px;
}

.sliceTileIndicator * {
  pointer-events: none;
}

.sliceTileIndicatorLarge {
  color: var(--outerspace);
  background: var(--good);
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  width: 10px;
  height: 10px;
  margin-top: 4px;
  margin-left: 8px;
  display: flex;
}

.sliceTileIndicatorLarge * {
  pointer-events: none;
}

.sliceTileIndicatorActive {
  background: var(--goodHighlight);
  box-shadow: 0px 0px 4px var(--goodHighlight);
}

.sliceTileIndicatorGood {
  background: var(--good);
  box-shadow: 0px 0px 4px var(--good);
}

.sliceTileIndicatorPending {
  background: var(--pending);
  box-shadow: 0px 0px 4px var(--pending);
}

.sliceTileIndicatorWarning {
  background: var(--warning);
  box-shadow: 0px 0px 4px var(--warning);
}

.sliceTileIndicatorBad {
  background: var(--bad);
  box-shadow: 0px 0px 4px var(--bad);
}

.sliceTileChangeChain {
  color: var(--outerspace);
  cursor: pointer;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  width: 41px;
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
}

.sliceTileChangeChain * {
  pointer-events: none;
}

.sliceTileChangeChain:hover {
  background: var(--ghostB);
}

.sliceTileChangeConnected {
  background: var(--good);
}

.sliceChainId {
  justify-content: center;
  align-items: center;
  margin: 10px;
  font-family: FiraCode;
  font-size: 12px;
  display: flex;
}

.sliceChainId .sliceChainIdIcon {
  margin-right: 6px;
}

.sliceContainer:last-child {
  margin: 0 6px;
}

.connectionTitle {
  justify-content: space-between;
  height: 26px;
  max-height: 26px;
}

.connectionOption {
  align-items: space-between;
  transition: var(--standard);
  margin: 2px;
  padding: 8px 16px;
  transform: scale(1);
}

.connectionOption .connectionOptionToggle {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 25px;
  display: flex;
  transform: translateZ(0);
}

.connectionOption .connectionOptionDetails {
  transition: var(--standard);
  opacity: 1;
  z-index: 40;
  transition-property: height, opacity;
  transition-delay: 0s, 0s;
  transition: var(--standard);
  pointer-events: none;
  margin-top: 0;
  padding: 8px 0;
  position: relative;
  transform: translateZ(0);
}

.connectionOption .connectionOptionDetails .connectionOptionDetailsInset {
  text-transform: uppercase;
  letter-spacing: 1px;
  z-index: 40;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 25px;
  font-size: 11px;
  font-weight: 400;
  display: flex;
}

.connectionOption .connectionOptionDetails .connectionOptionDetailsInset .connectionOptionStatus {
  height: 25px;
  display: flex;
}

.connectionOption .connectionOptionDetails .connectionOptionDetailsInset .connectionOptionStatus .connectionOptionStatusIndicator {
  align-items: center;
  height: 20px;
  margin-top: 5px;
  margin-right: 14px;
  display: flex;
}

.connectionOption .connectionOptionDetails .connectionOptionDetailsInset .connectionOptionStatus .connectionOptionStatusIndicator div {
  border-radius: 5.5px;
  width: 11px;
  height: 11px;
}

.connectionOption .connectionOptionDetails .connectionOptionDetailsInset .connectionOptionStatus .connectionOptionStatusIndicatorGood {
  background: var(--good);
}

.connectionOption .connectionOptionDetails .connectionOptionDetailsInset .connectionOptionStatus .connectionOptionStatusIndicatorBad {
  background: var(--bad);
}

.connectionOption .connectionOptionDetails .connectionOptionDetailsInset .connectionOptionStatus .connectionOptionStatusIndicatorPending {
  background: var(--pending);
}

.connectionOption .connectionOptionDetails .connectionOptionDetailsInset .connectionOptionStatus .connectionOptionStatusIndicatorWarning {
  background: var(--warning);
}

.connectionOption .connectionOptionDetails .connectionOptionDetailsInset .connectionOptionStatus .connectionOptionStatusText {
  align-items: center;
  height: 20px;
  margin-top: 5px;
