}

.connectionOption .connectionOptionDetails .connectionOptionDetailsInset .signerOptionSetWrap {
  height: 25px;
}

.connectionOption .connectionOptionDetails .connectionOptionDetailsInset .signerOptionSet {
  letter-spacing: 1px;
  background: var(--ghostD);
  height: 21px;
  box-shadow: cardDrop;
  border-radius: 10.5px;
