.connectionOption .connectionOptionDetails .connectionOptionDetailsInset .signerOptionSet:hover .signerOptionSetButton svg {
  opacity: .5;
}

.connectionOption .connectionOptionDetails .connectionOptionDetailsInset .signerOptionSet:hover .signerOptionSetButton svg:hover {
  opacity: 1;
}

.appInfo {
