  z-index: 20;
  padding-bottom: 50px;
  font-size: 14px;
  font-weight: 300;
  transform: translateZ(0);
}

.appInfo .appInfoIcon {
  transition: var(--standard);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 30px;
