  transform: translateZ(0);
}

.appInfo .appInfoViewLicense {
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 1px;
  width: 100%;
  color: var(--outerspace06);
  cursor: pointer;
  margin-top: 24px;
  font-size: 11px;
  font-weight: 400;
}

.appInfo .appInfoViewLicense:hover {
  cursor: pointer;
  color: var(--outerspace08);
}

.appInfo .appInfoLineReset {
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 1px;
